import React from 'react'
import PropTypes from 'prop-types'
import './TipContainer.scss'

const TipContainer = ({ children, tip, tipVisible = true }) => (
  <div className='TipContainer'>
    <div className='TipContainerchildren'>{children}</div>
    {tipVisible ? (
      <span className='TipContainerbubble'>
        <span className='TipContainerbubbleContent'>{tip}</span>
      </span>
    ) : null}
  </div>
)

TipContainer.propTypes = {
  children: PropTypes.node,
  tip: PropTypes.node,
  tipVisible: PropTypes.bool,
}

export default TipContainer
