import './RankingTable.scss'
import React from 'react'
import PropTypes from 'prop-types'
import { formattedAccuracyForRecord } from 'bemuse/rules/accuracy'

const RankingTable = ({ children }) => (
  <table className='RankingTable'>
    <tbody>{children}</tbody>
  </table>
)

RankingTable.propTypes = {
  children: PropTypes.any,
}

class Row extends React.Component {
  static propTypes = {
    record: PropTypes.object,
  }

  render() {
    return (
      <tr>
        <td className='RankingTablerank'>
          {this.props.record.rank || (
            <span title='Unable to determine your rank'>??</span>
          )}
        </td>
        <td className='RankingTablename'>{this.props.record.playerName}</td>
        <td className='RankingTablescore'>{this.props.record.score}</td>
        <td className='RankingTableaccuracy'>
          {formattedAccuracyForRecord(this.props.record)}
        </td>
      </tr>
    )
  }
}

export const Message = ({ children }) => (
  <tr>
    <td colSpan={4} className='RankingTablemessage'>
      {children}
    </td>
  </tr>
)

Message.propTypes = {
  children: PropTypes.any,
}

RankingTable.Row = Row
RankingTable.Message = Message

export default RankingTable
